import React from 'react';

import "./SubscriptionTerms.css";

const SubscriptionTerms = () => (
  <div className="subscription-terms">
    <p>
      (1) Offre valable en France métropolitaine jusqu&#39;au 31/12/2017 dans la limite des stocks disponibles. 
      Le tarif indiqué est garanti pour un an d&#39;abonnement. Au-beyond de la première année d&#39;abonnement, 
      il est susceptible d&#39;être modifié. En cas de cessation d&#39;activité, l&#39;association Debout se réserve
      le droit d&#39;interrompre votre abonnement; Aucuns frais ne va être remboursés. 
      Le 1er numéro de votre abonnement vous sera adressé dans un délai maximum de 3 mois à compter de la réception 
      de votre bulletin d&#39;abonnement et de son règlement. Vous disposez d&#39;un délai de 14 jours à compter de 
      la réception de votre magazine pour exercer votre droit de rétractation en notifiant, clairement et par courrier simple, 
      votre décision à l&#39;Association Debout, 10 rue Vergniaud, 92300 LEVALLOIS-PERRET. <br />
      Vos données sont traitées par l&#39;Association Debout pour l&#39;adhésion et la gestion de votre abonnement. 
      Les champs marqués d&#39;un * sont obligatoires; À défaut votre demande d&#39;abonnement devient caduque.
      Conformément à la loi du 6 janvier 1978 modifiée, vos droits d&#39;accès, de rectification ou de suppression, 
      pour motifs légitimes, peut-être exercés par courrier recommandé avec accusé de réception à l&#39;Association Debout,
      accompagné d&#39;une copie de votre pièce d&#39;identité. Vous pouvez également, pour des motifs légitimes, 
      vous opposer au traitement des données.
    </p>
    <p>
      (2) Pour être éligible à la réduction d&#39;impôt prévu par l&#39;article 200 du CGI, le montant de votre don 
      d&#39;être au moins 4 fois supérieur à celui de la contrepartie (ici votre abonnement individuel). Ainsi, 
      à partir de 40 € (10 € pour l&#39;abonnement + 30 € ou plus de dons en soutien à l&#39;Association Debout), 
      et vous êtes impossibles, le montant total de votre versement est déductible de votre impôt sur le revenu à
       hauteur de 66% de son montant, dans la limite de 20% de votre revenu imposable. Autrement dit, un montant total 
       (abonnement + don) de 50 € ne vous coûte réellement que 17 €, un montant de 90 € à 30,60 €, un montant de 120 € 
       à 40,80 €, etc.
      </p>
  </div>
)

export default SubscriptionTerms;


